import React from "react"

import { List, ListItem } from "@material-ui/core"

import { Link } from "react-router-dom"

import { AwesomeButton } from "react-awesome-button"

import "react-awesome-button/src/styles/styles.scss"

import "./../styles/Sidebar.scss"

const map = [
    ['/', 'Home'],
    ['/?page=get-started', 'Get Started'],
    ['/?page=redux-and-reduxers', 'Redux and Reduxers'],
    ['/?page=splitter', 'Splitter'],
    ['/?page=api', 'Api'],
    ['/?page=autobind', 'Autobind'],
]

export default props => (
    <div className='sidebar'>
        <List>
            {map.map(item => {
                const url = `/reactizy${item[0]}`
                return (
                <ListItem>
                    <Link to={url} onClick={() => props.update(url)}>
                        <AwesomeButton>{item[1]}</AwesomeButton>
                    </Link>
                </ListItem>
            )})}
        </List>
    </div>
)
