import React from "react"

import Sidebar from "./components/Sidebar"
import Header from "./components/Header"

import Home from "./pages/Home"
import Api from "./pages/Api"
import GetStarted from "./pages/GetStarted"
import Redux from "./pages/Redux"
import Autobind from "./pages/Autobind"
import Splitter from "./pages/Splitter"

import "./styles/App.scss"

class App extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            page: new URLSearchParams(document.location.search).get("page"),
        }

        this.updateUrl = this.updateUrl.bind(this)
    }

    componentDidUpdate() {
        const page = new URLSearchParams(document.location.search).get("page")
        if (this.state.page !== page) {
            this.setState({ page })
        }
    }

    updateUrl(page) {
        this.setState({page})
    }

    render() {
        console.log(this.state.page)
        const getPage = () => {
            if ([null, undefined].indexOf(this.state.page) !== -1) {
                return Home
            }

            switch (this.state.page) {
                case "api":
                    return Api
                case "get-started":
                    return GetStarted
                case "redux-and-reduxers":
                    return Redux
                case "splitter":
                    return Splitter
                case "autobind":
                    return Autobind
                default:
                    return Home
            }
        }

        const Page = getPage()

        return (
            <div className='App'>
                <Header />
                    <div className='wrapper'>
                        <Sidebar update={this.updateUrl}/>
                        <div className='content'>
                            <Page />
                        </div>
                    </div>
            </div>
        )
    }
}

export default App
